import { Component, Input } from '@angular/core';
import { toggleMark } from 'prosemirror-commands';

@Component({
  selector: 'app-super-sub-format',
  imports: [],
  templateUrl: './super-sub-format.html',
  styleUrl: './super-sub-format.css'
})

export class SuperSubFormat {
  @Input() content: any;
  
  // Superscript and Subscript functionality
  toggleSuperscript() {
    toggleMark(this.content.state.schema.marks['sup'])(this.content.state, this.content.dispatch);
    this.content.focus();
  }
  toggleSubscript() {
    toggleMark(this.content.state.schema.marks['sub'])(this.content.state, this.content.dispatch);
    this.content.focus();
  }
}
