import { NgFor } from '@angular/common';
import { Component, Input } from '@angular/core';
import { toggleMark } from 'prosemirror-commands';
import { ConfigService } from '../../../../shared/config.service';

@Component({
  selector: 'app-color-format',
  imports: [NgFor],
  templateUrl: './color-format.html',
  styleUrl: './color-format.css'
})

export class ColorFormat {
  @Input() content: any;

  constructor(private configService: ConfigService) { }

  colors: string[] = [];

  setFontColor(event: Event) {
    const color = (event.target as HTMLInputElement).value;
    const { state, dispatch } = this.content;
    const markType = state.schema.marks["textColor"];
    toggleMark(markType, { color })(state, dispatch);
    this.content.focus();
  }

  setBackgroundColor(event: Event) {
    const color = (event.target as HTMLInputElement).value;
    const { state, dispatch } = this.content;
    const markType = state.schema.marks["bgColor"];
    toggleMark(markType, { color })(state, dispatch);
    this.content.focus();
  }

  ngOnInit() {
    this.colors = this.configService.getConfig('colors') || [];
  }
}