import { keymap } from "prosemirror-keymap";
import { toggleMark } from "prosemirror-commands";
import { Schema } from "prosemirror-model";
import { redo, undo } from "prosemirror-history";

export function buildKeymap(schema: Schema) {
  const keys: Record<string, any> = {};

  // Basic formatting shortcuts
  if (schema.marks['bold']) keys["Mod-b"] = toggleMark(schema.marks['bold']);
  if (schema.marks['italic']) keys["Mod-i"] = toggleMark(schema.marks['italic']);
  if (schema.marks['underline']) keys["Mod-u"] = toggleMark(schema.marks['underline']);
  if (schema.marks['strike']) keys["Mod-Shift-x"] = toggleMark(schema.marks['strike']);
  if (schema.marks['sup']) keys["Mod-Shift-+"] = toggleMark(schema.marks['sup']);
  if (schema.marks['sub']) keys["Mod-="] = toggleMark(schema.marks['sub']);
  keys["Mod-z"] = undo;
  keys["Mod-y"] = redo;

  return keymap(keys);
}