import { Component, Input } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { EditorView } from 'prosemirror-view';
import { editorSchema } from '../../utils/editor-schema';
import { NgIf } from '@angular/common';

@Component({
  selector: 'app-image',
  standalone: true,
  imports: [FormsModule, NgIf],
  templateUrl: './image.component.html',
  styleUrl: './image.component.css'
})

export class ImageComponent {
  @Input() content!: EditorView;

  showImageDialog = false;
  imageUrl: string = '';
  imageAlt: string = 'image';
  imageTitle: string = 'image';
  imageWidth: string = 'auto';
  imageHeight: string = 'auto';

  schema = editorSchema;

  openImageDialog() {
    this.imageUrl = '';
    this.imageAlt = 'image';
    this.imageTitle = 'image';
    this.imageWidth = 'auto';
    this.imageHeight = 'auto';
    this.showImageDialog = true;
  }

  cancelImageDialog() {
    this.showImageDialog = false;
  }

  insertImage() {
    if (!this.imageUrl || !this.content) return;

    const { state, dispatch } = this.content;
    const { from, to } = state.selection;

    const imageNode = this.schema.nodes['image'].create({
      src: this.imageUrl,
      alt: this.imageAlt || 'image',
      title: this.imageTitle || 'image',
      width: this.imageWidth || '100%',
      height: this.imageHeight || 'auto',
      align: 'right',
    });

    dispatch(state.tr.replaceSelectionWith(imageNode).scrollIntoView());
    this.showImageDialog = false;
  }
}
