import { Component, Input } from '@angular/core';
import { EditorView } from 'prosemirror-view';
import { editorSchema } from '../../utils/editor-schema';
import { NgFor } from '@angular/common';
import { setBlockType } from 'prosemirror-commands';
import { ConfigService } from '../../../../shared/config.service';

@Component({
  selector: 'app-font-size',
  standalone: true,
  imports: [NgFor],
  templateUrl: './font-size.component.html',
  styleUrl: './font-size.component.css'
})

export class FontSizeComponent {
  @Input() content!: EditorView;
  fontSizes: number[] = [];
  headings: any = [];

  constructor(private configService: ConfigService) { }

  setBlockHeading(level: number | null) {
    const { state, dispatch } = this.content;
    const schema = state.schema;

    if (level === null) {
      setBlockType(schema.nodes['paragraph'])(state, dispatch);
    } else {
      const heading = schema.nodes['heading'];
      if (heading) {
        setBlockType(heading, { level })(state, dispatch);
      }
    }
    this.content.focus();
  }

  setFontSize(size: number) {
    if (!this.content) return;

    const markType: any = editorSchema.marks['fontSize'];
    if (!markType) return;

    const { state, dispatch } = this.content;
    const { tr, selection } = state;
    const { from, to, empty } = selection;
    // only apply when text is selected
    if (empty) return;
    // Remove existing fontSize marks in range
    tr.removeMark(from, to, markType);
    // Add the new size mark
    tr.addMark(from, to, markType.create({ size: `${size}px` }));

    dispatch(tr);
    this.content.focus();
  }

  ngOnInit() {
    this.headings = this.configService.getConfig('headings') || this.headings;
    this.fontSizes = this.configService.getConfig('fontSizes') || this.fontSizes;
  }
}
