  private colors = [
    "#b71c1c", "#d32f2f", "#e53935", "#f46136", "#f57c00", "#ff9800", "#ffb200", "#ffeb3b",
    "#1b5e20", "#388e3c", "#4caf50", "#81c784", "#0d47a1", "#1976d2", "#2196f3", "#64b5f6"
  ];

