import { Component, Input } from '@angular/core';
import { liftListItem, wrapInList } from 'prosemirror-schema-list';

@Component({
  selector: 'app-list-format',
  imports: [],
  templateUrl: './list-format.html',
  styleUrl: './list-format.css'
})

export class ListFormat {
  @Input() content: any;

  // Bullet and Numbered List functionality
  private isInList(state: any, type: string): boolean {
    const { $from } = state.selection;
    for (let d = $from.depth; d > 0; d--) {
      if ($from.node(d).type.name === type) return true;
    }
    return false;
  }

  toggleBulletList() {
    const { state, dispatch } = this.content;
    const schema = state.schema;
    const listNode = schema.nodes['bullet_list'];
    const listItem = schema.nodes['list_item'];

    if (this.isInList(state, 'bullet_list')) {
      liftListItem(listItem)(state, dispatch);
    } else {
      wrapInList(listNode)(state, dispatch);
    }
    this.content.focus();
  }

  toggleOrderedList() {
    const { state, dispatch } = this.content;
    const schema = state.schema;
    const listNode = schema.nodes['ordered_list'];
    const listItem = schema.nodes['list_item'];

    if (this.isInList(state, 'ordered_list')) {
      liftListItem(listItem)(state, dispatch);
    } else {
      wrapInList(listNode)(state, dispatch);
    }
    this.content.focus();
  }
}
