import { Component, Input } from '@angular/core';
import { toggleMark } from 'prosemirror-commands';
import { NgFor } from '@angular/common';
import { ConfigService } from '../../../../shared/config.service';

@Component({
  selector: 'app-font-family',
  standalone: true,
  imports: [ NgFor],
  templateUrl: './font-family.component.html',
  styleUrl: './font-family.component.css'
})

export class FontFamilyComponent {
  @Input() content!: any;
  fontFamilies: any = [];

  constructor(private configService: ConfigService) { }

  setFontFamily(event: Event) {
    if (!this.content) return;
    const font = (event.target as HTMLSelectElement).value;
    const { state, dispatch } = this.content;
    const markType = state.schema.marks.fontFamily;

    if (!markType) return;

    if (font) {
      toggleMark(markType, { font })(state, dispatch);
    } else {
      // remove font family mark if "default" is chosen
      toggleMark(markType, {})(state, dispatch);
    }

    this.content.focus();
  }

  ngOnInit() {
    this.fontFamilies = this.configService.getConfig('fontFamilies') || this.fontFamilies;
  }
}
