import { Component, ElementRef, Input, ViewChild } from '@angular/core';
import { EditorView } from 'prosemirror-view';
import { editorSchema } from './utils/editor-schema';
import { editorPlugins } from './utils/editor-plugins';
import { EditorState } from 'prosemirror-state';
import { DOMParser as PMDOMParser, DOMSerializer } from 'prosemirror-model';
import { FormsModule } from '@angular/forms';
import { ToolbarComponent } from './toolbar-component/toolbar-component';
import { NgFor } from '@angular/common';

@Component({
  selector: 'lib-inventyv-html-editor',
  standalone: true,
  imports: [NgFor, ToolbarComponent, FormsModule],
  templateUrl: './inventyv-html-editor.component.html',
  styleUrl: './inventyv-html-editor.component.css'
})

export class InventyvHtmlEditorComponent {
  @ViewChild('editorHost', { static: true }) editorHost!: ElementRef<HTMLDivElement>;

  // @Input() html: EditorView;
  @Input() contentTemplate: string | null = null;

  html!: EditorView;
  blank = `
<p><b>Bold</b> <i> Italic</i> <u> Underline</u> <s> Strikethrough</s></p>

<h2 style="font-family: 'Georgia';">Fonts & Sizes</h2>
<p style="font-family: 'Courier New'; font-size: 20px;">I use a different <b>font family</b> and <b>size</b> to look unique!</p>

<p>
 <b>Subscript:</b> H<sub>2</sub>O <b>Superscript:</b> E = mc<sup>2</sup>
</p>
<p style="color: red;">Text Red</p>
<p style="background-color: yellow;">Yellow Background</b></p>

<div><p style="text-align:left;">Alignment</p></div>
<p style="margin-left:40px;">Indentation</p>
<h2>Lists :</h2>
  <p>First</p>
  <p>Second</p>
  <p>Third</p>

<h2>Links & Images</h2>
<p>
  <a href="https://crm.mytaxprepoffice.com/login" target="_blank"><b>Visit CRM</b></a>
  <i>(Select for remove)</i>
</p>

<p style="text-align:left;">
  <img src="https://static.wixstatic.com/media/6f0edd_dd8e228109c44e3fbcfa180bc7d9fdec~mv2.png/v1/fill/w_110,h_66,al_c,q_85,usm_0.66_1.00_0.01,enc_avif,quality_auto/MTPO%20Orange.png"
       alt="MyTaxPrepOffice" width="100" style="border-radius:10px; box-shadow:0 0 10px rgba(0,0,0,0.2);" />
</p>
`;
  // contentTemplate: string = this.blank;

  ngOnInit(): void {
    const state = EditorState.create({
      schema: editorSchema,
      plugins: editorPlugins
    });

    this.html = new EditorView(this.editorHost.nativeElement, {
      state,
      dispatchTransaction: tr => {
        const newState = this.html.state.apply(tr);
        this.html.updateState(newState);
        this.contentTemplate = this.getHTML();
      }
    });
    // Load initial HTML
    this.setContentFromHTML(this.contentTemplate);
  }

  loadTemplate(template: string | null) {
    if (!template) return;
    this.contentTemplate = template;
    this.setContentFromHTML(template);
  }

  setContentFromHTML(html: string | null) {
    if (!html) return;
    const doc = new DOMParser().parseFromString(html, 'text/html');
    const pmDoc = PMDOMParser.fromSchema(this.html.state.schema).parse(doc.body);
    const tr = this.html.state.tr.replaceWith(0, this.html.state.doc.content.size, pmDoc);
    this.html.dispatch(tr);
  }

  getHTML(): string {
    const serializer = DOMSerializer.fromSchema(this.html.state.schema);
    const fragment = serializer.serializeFragment(this.html.state.doc.content);
    const wrapper = document.createElement('div');
    wrapper.appendChild(fragment);
    return wrapper.innerHTML;
  }
}
