import { MarkSpec, NodeSpec, Schema } from 'prosemirror-model';
import { schema as basicSchema } from 'prosemirror-schema-basic';
import { addListNodes } from 'prosemirror-schema-list';
import { tableNodes } from 'prosemirror-tables';

// with required cellAttributes
const myTableNodes = tableNodes({
    tableGroup: 'block',
    cellContent: 'block+',
    cellAttributes: {
        style: { default: null },
        class: { default: null },
        backgroundColor: {
            default: null, getFromDOM(dom: HTMLElement) {
                return dom.style.backgroundColor || null;
            },
            setDOMAttr(value, attrs) {
                if (value) attrs['style'] = (attrs['style'] || "") + `background-color:${value};`;
            }
        },
    },
});

// MARKS DEFINITIONS

// Font family
const fontFamilyMark: MarkSpec = {
    attrs: { font: { default: null } },
    parseDOM: [{ style: 'font-family', getAttrs: (value: string) => ({ font: value }) }],
    toDOM: mark => ['span', { style: `font-family: ${mark.attrs['font']}` }, 0]
};

// Text color and background color
const COLORS = [
    '#b71c1c', '#d32f2f', '#e53935', '#f46136', '#f57c00', '#ff9800', '#ffb200', '#ffeb3b', '#333',
    '#1b5e20', '#388e3c', '#4caf50', '#81c784', '#0d47a1', '#1976d2', '#2196f3', '#64b5f6'
];

function createColorMark(name: string, cssProp: string): MarkSpec {
    return {
        attrs: { color: {} },
        parseDOM: COLORS.map(c => ({ style: cssProp, getAttrs: value => ({ color: value }) })),
        toDOM: node => ['span', { style: `${cssProp}: ${node.attrs['color']}` }, 0]
    };
}

// Link mark
const linkMark: MarkSpec = {
    attrs: { href: {}, title: { default: null }, class: { default: null }, style: { default: null }, target: { default: '_blank' } },
    inclusive: false,
    parseDOM: [{
        tag: 'a[href]',
        getAttrs(dom: any) {
            return {
                href: dom.getAttribute('href'),
                title: dom.getAttribute('title'),
                class: dom.getAttribute('class'),
                style: dom.getAttribute('style'),
                target: dom.getAttribute('target') || '_blank',
            };
        }
    }],
    toDOM(node) {
        return ['a', { ...node.attrs }, 0];
    }
};

// NODE DEFINITIONS

// Paragraph with alignment
const paragraphWithAlignAndIndent: NodeSpec = {
    ...basicSchema.spec.nodes.get('paragraph'),
    attrs: { align: { default: 'left' }, indent: { default: 0 } },
    parseDOM: [{ tag: 'p', getAttrs: (dom: any) => ({ align: (dom as HTMLElement).style.textAlign || 'left',  indent: parseInt((dom as HTMLElement).style.marginLeft) / 30 || 0}) }],
    toDOM: (node: any) => ['p', { style: `text-align:${node.attrs.align}; margin-left:${node.attrs.indent * 30}px` }, 0]
};

// Heading with alignment
const headingWithAlign: NodeSpec = {
    ...basicSchema.spec.nodes.get('heading'),
    attrs: { ...basicSchema.spec.nodes.get('heading')?.attrs, align: { default: 'left' } },
    parseDOM: Array.from({ length: 6 }).map((_, i) => ({
        tag: `h${i + 1}`,
        getAttrs: (dom: any) => ({ level: i + 1, align: (dom as HTMLElement).style.textAlign || 'left' })
    })),
    toDOM: (node: any) => [`h${node.attrs.level}`, { style: `text-align:${node.attrs.align}` }, 0]
};

// Image node (resizable via style max-width)
const imageNode: NodeSpec = {
    inline: true,
    attrs: { src: {}, alt: { default: null }, title: { default: null }, width: { default: 'auto' }, height: { default: 'auto' }, align: { default: 'center' } },
    group: 'inline',
    draggable: true,
    parseDOM: [{
        tag: 'img[src]',
        getAttrs: (dom: any) => ({
            src: dom.getAttribute('src'),
            title: dom.getAttribute('title'),
            alt: dom.getAttribute('alt'),
            width: dom.getAttribute('width'),
            height: dom.getAttribute('height'),
            align: (dom as HTMLImageElement).style.textAlign || 'center',
        })
    }],
    toDOM: (node: any) => ['img', {
        src: node.attrs.src,
        title: node.attrs.title,
        alt: node.attrs.alt,
        width: node.attrs.width,
        height: node.attrs.height,
        style: 'max-width: {node.attrs.width}; height: {node.attrs.height}; cursor:nwse-resize; text-align:${node.attrs.align}',
    }]
};

